.btnNavbar{
  background-color: transparent;
  border: transparent;
  height: 4vh;
  font-size: 2vh;
  letter-spacing: 0.1vh;
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  background: -webkit-linear-gradient(yellow 42%, yellow, yellow, yellow);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  cursor: pointer;
  font-family: 'Tomorrow', sans-serif;
  font-weight: 900;


}

.btnNavbar:hover{
  transition: all 0.2s ease-in-out;
 
  opacity: 0.7;
}

.about{
  color: #c940b4;
  font-weight: 900;
}

.roadmap{
  color: #177cbf;
  font-weight: 900;
}

.navbar{
  background-color: transparent;

}

.navbar.colorChange{
  background-color: #0f1923;

  transition: all 0.4s ease-in-out;
}